<template>
  <div id="manage-course" :style="{minHeight}">
    <div class="left">
      <h1 class="el-icon-menu" @click="$router.push({ name: 'manage'})" data-text="" data-text-hover=" "></h1>
      <a ref='left' v-for="item in left" @click="item.click">
        {{item.tag}}
      </a>
    </div>
    <div class="right">
      <transition name="fade">
        <div id="courses" v-if="select.length === 1">
        <el-input
          :placeholder="`${step}${step}`"
          class="filter"
          v-model="search"
          size="small">
          <el-button slot="append" icon="el-icon-plus" @click="appendCourse"></el-button>
        </el-input>
          <div class="cards" v-if="searchFilter(courses).length">
            <div v-for="c in searchFilter(courses)" @click="courseCardClick(c)" class="cardBox">
              <el-card :body-style="{ padding: '0px' }">
                <div class="img">
                  <img :src="c.cover_image" class="image">
                </div>
                <div class="detail">
                  <div class="title">{{c.title}}</div>
                  <div class="subtitle">{{c.subtitle}}</div>
                </div>
              </el-card>
            </div>
          </div>
          <div class="none" v-else style="margin-left: 10%;">{{step}}</div>
        </div>
      </transition>

      <transition name="fade">
        <div id="course" v-if="select.length === 2">

          <el-form
            :model="courseForm"
            label-width="80px"
            label-position="top"
            :rules="courseRules"
            ref="courseForm"
          >
            <el-form-item label="" prop="cover_image">
              <el-upload
                :action="uploadTo"
                :on-success="onUploadSuccess"
                :show-file-list="false">
                <i v-if="courseForm.cover_image === 'uploading'" class="el-icon-loading"></i>
                <img v-else-if="courseForm.cover_image" :src="courseForm.cover_image">
                <i v-else class="el-icon-plus"></i>
                <div slot="tip" class="el-upload__tip"></div>
              </el-upload>
            </el-form-item>

            <el-form-item label="" prop="title">
              <el-input v-model="courseForm.title"></el-input>
            </el-form-item>
            <el-form-item label="" prop="subtitle">
              <el-input v-model="courseForm.subtitle"></el-input>
            </el-form-item>
            <el-form-item label="" prop="introduction">
              <el-input v-model="courseForm.introduction"  type="textarea"
                        :autosize="{ minRows: 2, maxRows: 4}" resize="none"></el-input>
            </el-form-item>
            <el-form-item label="" prop="top">
              <el-switch
                v-model="courseForm.top">
              </el-switch>
            </el-form-item>
            <el-form-item class="save">
              <el-button type="primary" @click="saveCourse"></el-button>
              <el-button plain type="primary" @click="removeCourse"></el-button>
            </el-form-item>

            <div class="divide"></div>

            <el-form-item label="" class="changeChapter">
              <el-input
                :placeholder="`${step}${step}`"
                class="filter"
                v-model="search"
                size="small">
                <el-button slot="append" icon="el-icon-plus" @click="appendChapter"></el-button>
              </el-input>

              <div class="cards" v-if="searchFilter(chapters).length">
                <div class="cardBox" v-for="c in searchFilter(chapters)" @click="chapterCardClick(c)">
                  <el-card>
                    <span>{{c.title}}</span>
                    <i class="el-icon-edit"></i>
                  </el-card>
                </div>
              </div>
              <div class="none" v-else>{{step}}</div>
            </el-form-item>
          </el-form>
        </div>
      </transition>


      <transition name="fade">
        <div id="chapter" v-if="select.length === 3">
          <el-form
            :model="chapterForm"
            label-width="80px"
            label-position="top"
            :rules="chapterRules"
            ref="chapterForm"
          >


            <el-form-item label="" prop="title">
              <el-input v-model="chapterForm.title"></el-input>
            </el-form-item>
            <el-form-item label="" prop="description">
              <el-input v-model="chapterForm.description" type="textarea"
                        :autosize="{ minRows: 1, maxRows: 4}" resize="none"></el-input>
            </el-form-item>
            <el-form-item class="save">
              <el-button type="primary" @click="saveChapter"></el-button>
              <el-button plain type="primary" @click="removeChapter"></el-button>
            </el-form-item>


            <div class="divide"></div>

            <el-form-item label="">
              <el-input
                :placeholder="`${step}${step}`"
                class="filter"
                v-model="search"
                size="small">
                <el-button slot="append" icon="el-icon-plus" @click="appendUnit"></el-button>
              </el-input>

              <div class="cards" v-if="searchFilter(units).length">
                <div class="cardBox" v-for="c in searchFilter(units)" @click="unitCardClick(c)">
                  <el-card>
                    <span>{{c.title}}</span>
                    <i class="el-icon-edit"></i>
                  </el-card>
                </div>
              </div>
              <div class="none" v-else>{{step}}</div>
            </el-form-item>
          </el-form>
        </div>
      </transition>


      <transition name="fade">
        <div id="unit" v-if="select.length === 4">

          <el-form
            :model="unitForm"
            label-width="80px"
            label-position="top"
            :rules="unitRules"
            ref="unitForm"
          >
            <el-form-item label="" prop="title">
              <el-input v-model="unitForm.title"></el-input>
            </el-form-item>
            <el-form-item label="" prop="description">
              <el-input v-model="unitForm.description" type="textarea"
                        :autosize="{ minRows: 1, maxRows: 4}" resize="none"></el-input>
            </el-form-item>
            <el-form-item class="save">
              <el-button type="primary" @click="saveUnit"></el-button>
              <el-button plain type="primary" @click="removeUnit"></el-button>
            </el-form-item>

            <div class="divide"></div>

            <el-form-item label="">
              <el-upload
                :action="uploadTo"
                :on-success="onVideoUploadSuccess"
                :on-progress="onVideoUploadProgress"
                :on-remove="onVideoUploadRemove"
                :file-list="videos">
                <el-button size="small" type="primary" :disabled="uploading || unit.id < 0"></el-button>
                <div slot="tip" class="el-upload__tip">flv</div>
              </el-upload>
            </el-form-item>
          </el-form>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
  import FootBar from '../../components/footbar'
  import Navbar from '../../components/navbar/index.vue'
//  import store from './store'
//  import {debounce} from 'lodash'
  import { VueEditor } from 'vue2-editor'
  import {server} from '../../config'
  import urlJoin from 'url-join'

  export default {
    name: 'manage-course',
    components: {
      FootBar,
      Navbar,
      VueEditor
    },
    watch: {
      course: {
        deep: true,
        handler (val) {
          this.courseForm = Object.assign({}, val)
        }
      },
      chapter: {
        deep: true,
        handler (val) {
          this.chapterForm = Object.assign({}, val)
        }
      },
      unit: {
        deep: true,
        handler (val) {
          this.unitForm = Object.assign({}, val)
        }
      },
      homework: {
        deep: true,
        handler (val) {
          this.homeworkForm = Object.assign({}, this.homeworkForm, val)
        }
      }
    },
    computed: {
      courses () {
        return this.$store.state.manage.courses
      },
      chapters () {
        return this.$store.state.manage.chapters
      },
      units () {
        return this.$store.state.manage.units
      },
      videos () {
        const v = this.$store.state.manage.unit.videos
        return v && v.map(({title, id}) => ({id, name: title}))
      },
      course () {
        return this.$store.state.manage.course
      },
      chapter () {
        return this.$store.state.manage.chapter
      },
      unit () {
        return this.$store.state.manage.unit
      },
      homework () {
        return this.$store.state.manage.homework
      },
      step () {
        const u = ['', '', '', '']
        return u[this.select.length - 1]
      }
    },
    data () {
      return {
        select: [-1],
        search: '',
        left: [
          {
            tag: '',
            click: () => {
              this.$store.dispatch('getAllCourses')
              this.$store.commit('clearCourse')
              while (this.select.length) this.select.pop()
              this.select.push(-1)
              this.search = ''
              this.left = this.left.slice(0, 1)
            }
          }
        ],
        afterclass: 0,
        courseForm: {},
        chapterForm: {},
        unitForm: {},
        homeworkForm: {},
        uploadTo: server.upload,
        uploading: false,
        imgPreview: '',

        courseRules: {
          cover_image: [
            {required: true, message: '', trigger: 'blur'}
          ],
          title: [
            {required: true, message: '', trigger: 'blur'}
          ],
          subtitle: [
            {required: true, message: '', trigger: 'blur'}
          ],
          introduction: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        chapterRules: {
          title: [
            {required: true, message: '', trigger: 'blur'}
          ],
          description: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },

        unitRules: {
          title: [
            {required: true, message: '', trigger: 'blur'}
          ],
          description: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        minHeight: 0

      }
    },
    methods: {
      onUploadSuccess (res, file, fileList) {
        const t = res.startsWith('http') ? res : urlJoin(server.host, res)
        this.$set(this.courseForm, 'cover_image', t)
      },
      onVideoUploadProgress (event, file, fileList) {
        //        this.$set(this.courseForm, 'cover_image', 'uploading')
        this.uploading = true
      },
      onVideoUploadSuccess (res, file, fileList) {
        this.uploading = false
        console.log(res, file, fileList)
        const t = res.startsWith('http') ? res : urlJoin(server.host, res)
        this.$store.dispatch('addVideo', {url: t, fileName: file.name, teacher: this.$store.state.session.userId})
      },
      onVideoUploadRemove (file, fileList) {
        this.uploading = false
        console.log(file, fileList)
        this.$store.dispatch('removeVideo', file)
      },

      saveCourse () {
        this.$refs.courseForm.validate(valid => {
          if (valid) {
            const f = Object.assign({}, this.courseForm, {obligator: this.$store.state.session.userId})
            this.$store.dispatch('submitCourseForm', {
              courseForm: f,
              cb: () => {
                this.$refs.left[0].click()
              }
            })
          } else {
            return false
          }
        })
      },
      saveChapter () {
        this.$refs.chapterForm.validate(valid => {
          if (valid) {
            const f = Object.assign({}, this.chapterForm, {leacturer: this.$store.state.session.userId})
            this.$store.dispatch('submitChapterForm', {
              course: this.course.id,
              chapterForm: f,
              cb: () => {
                this.$refs.left[1].click()
              }
            })
          } else {
            return false
          }
        })
      },
      saveUnit () {
        this.$refs.unitForm.validate(valid => {
          if (valid) {
            const f = Object.assign({}, this.unitForm, {leacturer: this.$store.state.session.userId})
            this.$store.dispatch('submitUnitForm', {
              chapter: this.chapter.id,
              unitForm: f,
              cb: () => {
                this.$refs.left[2].click()
              }
            })
          } else {
            return false
          }
        })
      },
      removeCourse () {
        this.$confirm(` ${this.course.title}, ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$store.dispatch('removeCourse', {
            course: this.course,
            cb: () => {
              this.$refs.left[0].click()
            }
          })
        })
      },
      removeChapter () {
        this.$confirm(` ${this.chapter.title}, ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$store.dispatch('removeChapter', {
            id: this.chapter.id,
            cb: () => {
              this.$refs.left[1].click()
            }
          })
        })
      },
      removeUnit () {
        this.$confirm(` ${this.unit.title}, ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$store.dispatch('removeUnit', {
            id: this.unit.id,
            cb: () => {
              this.$refs.left[2].click()
            }
          })
        })
      },

      courseCardClick (c) {
        c = c || this.$store.state.manage.courses.slice(-1)[0]
        this.select[0] = c.id
        this.$store.dispatch('getCourse', c)
        this.select.push(-1)
        this.search = ''
        this.left.push(
          {
            tag: '',
            click: () => {
              this.$store.dispatch('getCourse', c)
              while (this.select.length > 1) this.select.pop()
              this.select.push(-1)
              this.search = ''
              this.$store.commit('clearChapter')
              this.left = this.left.slice(0, 2)
            }
          }
        )
      },
      chapterCardClick (c) {
        c = c || this.$store.state.manage.chapters.slice(-1)[0]
        this.select[1] = c.id
        this.$store.dispatch('getChapter', c)
        this.select.push(-1)
        this.search = ''
        this.left.push({
          tag: '',
          click: () => {
            this.$store.dispatch('getChapter', c)
            while (this.select.length > 2) this.select.pop()
            this.select.push(-1)
            this.search = ''
            this.$store.commit('clearUnit')
            this.left = this.left.slice(0, 3)
          }
        })
      },
      unitCardClick (c) {
        c = c || this.$store.state.manage.units.slice(-1)[0]
        this.select[2] = c.id
        this.$store.dispatch('getUnit', c)
        this.select.push(-1)
        this.search = ''
        this.left.push({
          tag: '',
          click: () => {

          }
        })
      },

      appendCourse () {
        this.courseForm = Object()
        this.$store.dispatch('appendBlankCourse')
        this.courseCardClick()
      },
      appendChapter () {
        this.chapterForm = Object()
        this.$store.dispatch('appendBlankChapter')
        this.chapterCardClick()
      },
      appendUnit () {
        this.unitForm = Object()
        this.$store.dispatch('appendBlankUnit')
        this.unitCardClick()
      },

      searchFilter (val, key = 'title') {
        val = val || [] // Ready for empty
        let l = val.filter(x => x[key].search(this.search) > -1)
        if (l.length === 0) {
          l = val
        }
        return l
      }
    },
    mounted () {
      this.$store.dispatch('getAllCourses')
      this.minHeight = window.innerHeight - 96 + 'px'
    }
  }
</script>



